@import 'variables', 'colors', 'fonts';

@mixin phone {
    @media (max-width: $phone-width) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: $tablet-width) {
        @content;
    }
}

@mixin desktop {
    @media (max-width: $desktop-width) {
        @content;
    }
}